body, html{
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

.App{
  max-width: 900px;
  padding: 0 10px;
  margin: 0 auto;
  font-family: 'Roboto', sans-serif;
}

.box-brand{
  padding: 30px 0;
  text-align: center;
}

.box-brand img{
  display: inline-block;
  margin: 0 auto;
}

h6{
  font-size: 18px;
  text-align: center;
}

form{
  max-width: 300px;
  margin: 0 auto;
}

input{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  padding: 10px;
  display: block;
  margin-bottom: 15px;
  width: 100%;
  box-sizing: border-box;
  border-radius: 10px;
  outline: none;
  border: 1px solid #000000;
}

.button-submit{
  color: #ffffff;
  background-color: #1cdb7a;
  border: none;
  cursor: pointer;
}

.button-submit:hover{
  background-color: #1373FA;
}

.modal-loading{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0,0,0, .7);
}

.modal-loading span{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 5px;
  background: #ffffff;
}